<template>
<div class="particle" id="particle">
</div>
</template>

<script>
import * as THREE from 'three'
import TWEEN from '@tweenjs/tween.js'

export default {
  name: 'MainParticle',
  data() {
    return {
      camera: null,
      scene: null,
      renderer: null,
      mouseX: 0,
      mouseY: 0,
      container: null
    }
  },
  mounted() {
    this.init()
    this.animate()
  },
  methods: {
    init() {
      this.container = document.getElementById('particle')
      this.renderer = new THREE.WebGLRenderer({
        antialias: true
      })
      this.renderer.setSize(this.container.clientWidth, this.container.clientHeight)
      // eslint-disable-next-line
      console.log(this.container.clientWidth, this.container.clientHeight)

      this.camera = new THREE.PerspectiveCamera(75, this.container.clientWidth / this.container.clientHeight, 1, 5000)
      this.camera.position.z = 1000
      this.scene = new THREE.Scene()
      let material = new THREE.SpriteMaterial({
        map: new THREE.CanvasTexture(this.generateSprite()),
        blending: THREE.AdditiveBlending
      });
      for (var i = 0; i < 1000; i++) {
        let particle = new THREE.Sprite(material)
        this.initParticle(particle, i * 10)
        this.scene.add(particle)
      }
      this.renderer.setPixelRatio(this.container.devicePixelRatio)
      this.container.appendChild(this.renderer.domElement)
      document.addEventListener('mousemove', this.onDocumentMouseMove, false)
      document.addEventListener('touchstart', this.onDocumentTouchStart, false)
      document.addEventListener('touchmove', this.onDocumentTouchMove, false)
      window.addEventListener('resize', this.onWindowResize, false)
    },
    onWindowResize() {
      this.windowHalfX = this.container.clientWidth / 2
      this.windowHalfY = this.container.clientHeight / 2
      this.camera.aspect = this.container.clientWidth / this.container.clientHeight
      this.camera.updateProjectionMatrix()
      this.renderer.setSize(this.container.clientWidth, this.container.clientHeight)
    },
    generateSprite() {
      const canvas = document.createElement('canvas')
      canvas.width = 16
      canvas.height = 16
      var context = canvas.getContext('2d')
      var gradient = context.createRadialGradient(canvas.width / 2, canvas.height / 2, 0, canvas.width / 2, canvas.height / 2, canvas.width / 2)
      gradient.addColorStop(0, 'rgba(255,255,255,1)')
      gradient.addColorStop(0.2, 'rgba(0,255,255,1)')
      gradient.addColorStop(0.4, 'rgba(0,0,64,1)')
      gradient.addColorStop(1, 'rgba(0,0,0,1)')
      context.fillStyle = gradient
      context.fillRect(0, 0, canvas.width, canvas.height)
      return canvas
    },
    initParticle(particle, delay) {
      var p = this instanceof THREE.Sprite ? this : particle
      var d = delay !== undefined ? delay : 0
      p.position.set(0, 0, 0)
      p.scale.x = p.scale.y = Math.random() * 32 + 16
      new TWEEN.Tween(p)
        .delay(d)
        .to({}, 10000)
        .onComplete(this.initParticle)
        .start()
      new TWEEN.Tween(p.position)
        .delay(d)
        .to({
          x: Math.random() * 4000 - 2000,
          y: Math.random() * 1000 - 500,
          z: Math.random() * 4000 - 2000
        }, 10000)
        .start()
      new TWEEN.Tween(p.scale)
        .delay(d)
        .to({
          x: 0.01,
          y: 0.01
        }, 10000)
        .start()
    },
    onDocumentMouseMove(event) {
      this.mouseX = event.clientX - this.windowHalfX
      this.mouseY = event.clientY - this.windowHalfY
    },
    onDocumentTouchStart(event) {
      if (event.touches.length == 1) {
        event.preventDefault()
        this.mouseX = event.touches[0].pageX - this.windowHalfX
        this.mouseY = event.touches[0].pageY - this.windowHalfY
      }
    },
    onDocumentTouchMove(event) {
      if (event.touches.length == 1) {
        event.preventDefault()
        this.mouseX = event.touches[0].pageX - this.windowHalfX
        this.mouseY = event.touches[0].pageY - this.windowHalfY
      }
    },
    animate() {
      requestAnimationFrame(this.animate)
      this.render()
    },
    render() {
      TWEEN.update()
      this.camera.position.x += (this.mouseX - this.camera.position.x) * 0.05
      this.camera.position.y += (-this.mouseY - this.camera.position.y) * 0.05
      this.camera.lookAt(this.scene.position)
      this.renderer.render(this.scene, this.camera)
    }
  }
}
</script>

<style>
.particle {
  height: 500px;
  background-color: #000000;
  margin: 0px;
  overflow: hidden;
}

.a {
  color: #0078ff;
}
</style>
